<template>
  <div class="volunteer-box">
    <el-dialog title="" :visible.sync="volunteerVisible" :before-close="closeDialog">
      <el-form label-width="100px">
        <el-col>
          <el-form-item label="" style="font-size: 20px">
            <el-input v-model="zyz.name" clearable placeholder="" style="width: 70%;">
              <el-button slot="append" @click="queryzyz"></el-button>
            </el-input>
          </el-form-item>
        </el-col>
      </el-form>
      <template>
        <div class="zyz">
          <el-table
            ref="multipleTableVolunteer"
            :data="volunteer"
            style="width: 100%"
            @selection-change="handleSelecetVolunteer"
          >
            <el-table-column type="selection" width="55" center></el-table-column>
            <el-table-column prop="name" label="" width="120"></el-table-column>
            <el-table-column prop="idCard" label="" width="220"></el-table-column>
            <el-table-column prop="mobile" label="" width="200"></el-table-column>
            <el-table-column prop="userTypeName" label=""></el-table-column>
          </el-table>
        </div>
      </template>
      <el-pagination
        @size-change="handleSizeVolunteer"
        @current-change="handleCurrentVolunteer"
        :current-page="volunteerCurrentPage"
        :page-size="volunteerPagesize"
        :total="volunteerTotal"
        background
        layout="total,sizes,prev, pager, next ,jumper"
      ></el-pagination>
      <template>
        <el-form ref="zyz" :model="zyz" label-width="100%" v-if="this.category == '1'">
          <p style="font-size: 20px;color: black;margin: 20px 0"></p>
          <el-radio-group v-model="zyz.userType">
            <el-row style="margin-bottom: 20px;margin-left: 50px">
              <el-col :span="8">
                <el-radio :label="1"></el-radio>
              </el-col>
              <el-col :span="8">
                <el-radio :label="2" style="margin-left: 30px"></el-radio>
              </el-col>
              <el-col :span="8">
                <el-radio :label="3" style="margin-left: 30px"></el-radio>
              </el-col>
            </el-row>
            <el-row style="margin-left: 50px">
              <el-col :span="8">
                <el-radio :label="4"></el-radio>
              </el-col>
              <el-col :span="8">
                <el-radio :label="5" style="margin-left: 30px"></el-radio>
              </el-col>
              <el-col :span="8">
                <el-radio :label="6" style="margin-left: 30px"></el-radio>
              </el-col>
            </el-row>
          </el-radio-group>
        </el-form>
      </template>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleVolunteer('zyz')"> </el-button>
        <el-button type="primary" @click="saveAddVolunteer"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { zyzpage } from "@/api/zcpa/actProject";
export default {
  props: {
    dictArrayObj: {
      type: Object
    },
    category: {
      type: String
    },
    zyzaidTargetType: {
      type: String
    },
    volunteercode: {
      type: String
    }
  },
  watch: {
    dictArrayObj: {
      handler(newName, oldName) {
        this.initDictData(newName);
      },
      immediate: true, //
      deep: true
    },
    volunteercode: {
      handler(newName, oldName) {
        this.areaCode = newName;
        this.getVolunteerData();
      },
      immediate: true, //
      deep: true
    },
    category: {
      handler(newName, oldName) {
        if (newName) {
          this.category = newName;
        }
      }
    },
    zyzaidTargetType: {
      handler(newName, oldName) {
        this.zyzaidTargetType = newName;
      },
      immediate: true, //
      deep: true
    }
  },
  name: "volunteer",
  data() {
    return {
      volunteer: [],
      peopleValueArr: [],
      volunteerVisible: false, //
      volunteerCurrentPage: 1, //
      volunteerPagesize: 10, //
      volunteerTotal: 0, //
      zyz: {
        name: "",
        userType: ""
      }
    };
  },
  methods: {
    //
    handleSelecetVolunteer(val) {
      this.volunteerArr = val;
    },
    handleSizeVolunteer: function(size) {
      this.volunteerPagesize = size;
      this.getVolunteerData();
      //
    },
    handleCurrentVolunteer: function(currentPage) {
      this.volunteerCurrentPage = currentPage;
      //
      this.getVolunteerData();
    },
    //
    saveAddVolunteer() {
      this.$emit("saveAddVolunteer");
    },
    //
    handleVolunteer(type) {
      this.zyz.name = "";
      this.volunteerVisible = false;
      this.getVolunteerData();
    },
    closeDialog() {
      this.zyz.name = "";
      this.volunteerVisible = false;
      this.getVolunteerData(); //
    },
    //
    getVolunteerData() {
      var obj = {};
      obj.pageNum = this.volunteerCurrentPage;
      obj.pageSize = this.volunteerPagesize;
      obj.totalRecord = this.volunteerTotal;
      obj.name = this.zyz.name;
      obj.areaCode = this.areaCode;
      this.volunteerCurrentPage = 1;
      if (this.category == "1" && this.zyzaidTargetType != 6) {
        obj.userType = this.zyzaidTargetType;
      } else {
        obj.userType = "";
      }
      zyzpage(obj).then(response => {
        if (response.status == 1) {
          this.volunteer = response.rows;
          this.volunteerTotal = response.total;
          if (this.volunteer) {
            for (var i = 0; i < this.volunteer.length; i++) {
              for (var y = 0; y < this.peopleValueArr.length; y++) {
                if (this.volunteer[i].userType == this.peopleValueArr[y].code) {
                  this.volunteer[i].userType = this.peopleValueArr[y].code;
                  this.volunteer[i].userName = this.peopleValueArr[y].name;
                }
              }
            }
          }
        }
      });
    },
    //
    queryzyz() {
      this.getVolunteerData();
    },
    //
    initDictData() {
      this.peopleValueArr = this.dictArrayObj.peopleValue; //49
    }
  }
};
</script>

<style lang="less" scoped>
</style>