<template>
  <div class="table">
    <div class="container">
      <div class="handle-box">
        <el-row :gutter="24" style="padding:0; margin-right:-4px;">
          <el-col :span="2" v-if="this.areaCode == '4307'">
            <el-button
              type="primary"
              class="handle-del mr11"
              @click="handleAdd(null,'add')"
              v-if="userBtn['/zcpa/project/create']"
            >{{userBtn['/zcpa/project/create']}}</el-button>
          </el-col>
          <el-col :offset="2" :span="3" v-if="this.areaCode == '4307'">
            <el-cascader
              placeholder=""
              v-model="xzZoning"
              :options="cityCountyOptions"
              :props="areaCodeProps"
              clearable
              change-on-select
              filterable
              class="handle-select mr11"
            />
          </el-col>
          <el-col :span="7" v-if="this.areaCode != '4307'">&nbsp;</el-col>
          <el-col :span="3">
            <el-select
              v-model="query.projectLevel"
              clearable
              placeholder=""
              class="handle-select mr11"
            >
              <el-option
                v-for="item in projectLevelArr"
                :key="item.code"
                :label="item.name"
                :value="item.code"
              ></el-option>
            </el-select>
          </el-col>
          <el-col :span="3">
            <el-select
              v-model="query.category"
              clearable
              placeholder=""
              class="handle-select mr11"
            >
              <el-option
                v-for="item in projectTypeArr"
                :key="item.code"
                :label="item.name"
                :value="item.code"
              ></el-option>
            </el-select>
          </el-col>
          <el-col :span="3">
            <el-select
              v-model="query.projectStateType"
              clearable
              placeholder=""
              class="handle-select mr11"
              @change="changeState"
            >
              <el-option
                v-for="item in statusValueArr"
                :key="item.code"
                :label="item.name"
                :value="item.code"
              ></el-option>
            </el-select>
          </el-col>
          <el-col :span="3">
            <el-date-picker
              v-model="query.startTime"
              type="date"
              :picker-options="startTime"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              placeholder=""
              class="handle-input mr10"
            ></el-date-picker>
          </el-col>
          <el-col :span="3">
            <el-date-picker
              v-model="query.endTime"
              type="date"
              :picker-options="endTime"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              placeholder=""
              class="handle-input mr10"
            ></el-date-picker>
          </el-col>
          <el-col :span="2" style="padding:0;float:right;text-align: right;">
            <el-button
              style="margin-right:12px; "
              type="primary"
              icon="el-icon-search"
              @click="search"
            ></el-button>
          </el-col>
        </el-row>
      </div>
      <el-table :data="rows" border max-height="475" class="table-main" ref="multipleTable">
        <el-table-column type="index" width="55" label="" align="center"></el-table-column>
        <el-table-column prop="projectName" label="" width="400"></el-table-column>
        <el-table-column prop="categoryType" label="" width="150"></el-table-column>
        <el-table-column prop="projectLevel" label="" sortable width="150">
          <template slot-scope="scope">
            <div type="text" v-if="scope.row.projectLevel=='0'"></div>
            <div type="text" v-if="scope.row.projectLevel=='1'"></div>
          </template>
        </el-table-column>
        <el-table-column prop="areaName" label="" sortable width="150"></el-table-column>
        <el-table-column
          type="date"
          value-format="yyyy-MM-dd"
          prop="startTime"
          label=""
          sortable
          width="180"
        ></el-table-column>
        <el-table-column prop="endTime" label="" sortable width="180"></el-table-column>
        <el-table-column prop="projectState" label="" sortable align="center" width="100">
          <template slot-scope="scope">
            <div type="text" class="orange" v-if="scope.row.projectState=='1'"></div>
            <div type="text" class="green" v-if="scope.row.projectState=='2'"></div>
            <div type="text" class="red" v-if="scope.row.projectState=='3'"></div>
            <div type="text" class="gray" v-if="scope.row.projectState=='4'"></div>
          </template>
        </el-table-column>
        <el-table-column label="" fixed="right" width="240" align="right" header-align="center">
          <template slot-scope="scope">
            <el-button
              type="text"
              v-if="userBtn['/zcpa/project/docking'] && scope.row.projectState !='1' && scope.row.projectState !='3'"
              @click="handleDocking(scope.row)"
            >{{userBtn['/zcpa/project/docking']}}</el-button>
            <span
              class="strightline"
              v-if="userBtn['/zcpa/project/docking'] && scope.row.projectState !='1'  && scope.row.projectState !='3'"
            >|</span>
            <el-button
              type="text"
              v-if="userBtn['/zcpa/project/declare'] && scope.row.projectLevel=='0' && scope.row.projectState=='2'"
              @click="handleAdd(scope.row,'declare')"
            >{{userBtn['/zcpa/project/declare']}}</el-button>
            <span
              v-if="userBtn['/zcpa/project/declare'] && scope.row.projectLevel=='0' && scope.row.projectState=='2'"
              class="strightline"
            >|</span>
            <el-button
              type="text"
              v-if="userBtn['/zcpa/project/check'] &&scope.row.projectState=='1' && scope.row.userAreaCode =='4307'"
              @click="handleExamine(scope.$index, scope.row)"
            >{{userBtn['/zcpa/project/check']}}</el-button>
            <span
              class="strightline"
              v-if="userBtn['/zcpa/project/check'] && scope.row.projectState=='1' && scope.row.userAreaCode =='4307' "
            >|</span>
            <el-button
              type="text"
              v-if="userBtn['/zcpa/project/update'] && scope.row.areaCode.includes(scope.row.userAreaCode) && scope.row.projectLevel=='0' && scope.row.projectState=='2' || scope.row.projectLevel=='1' && scope.row.projectState!='4'"
              @click="handleAdd(scope.row,'edit')"
            >{{userBtn['/zcpa/project/update']}}</el-button>
            <span
              v-if="userBtn['/zcpa/project/update'] && scope.row.areaCode.includes(scope.row.userAreaCode) && scope.row.projectLevel=='0' && scope.row.projectState=='2' || scope.row.projectLevel=='1' && scope.row.projectState!='4'"
              class="strightline"
            >|</span>

            <el-button
              type="text"
              v-if="userBtn['/zcpa/project/delect']"
              @click="handleDelete(scope.$index, scope.row)"
            >{{userBtn['/zcpa/project/delect']}}</el-button>
            <span class="strightline" v-if="userBtn['/zcpa/project/delect']">|</span>
            <el-button type="text" @click="handleLook(scope.$index,scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pagesize"
        :total="total"
        background
        layout="total,sizes,prev, pager, next ,jumper"
      ></el-pagination>
    </div>
    <!--  -->
    <el-dialog title="" class="examine" :visible.sync="examineVisible">
      <el-form ref="form" :model="updateState" label-width="100%">
        <p style="line-height: 50px; text-align: center; font-size: 20px"></p>
        <div style="text-align: center">
          <el-radio-group v-model="updateState.projectState" style="text-align: center">
            <el-radio :label="2"></el-radio>
            <el-radio :label="3"></el-radio>
          </el-radio-group>
        </div>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="examineVisible = false"> </el-button>
        <el-button type="primary" @click="saveExamine"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <basic
      ref="basicData"
      :dictArrayObj="dictArrayObj"
      :submitType="submitType"
      :cityCountyOptions="cityCountyOptions"
      :title="titleName"
      :byIdData="byIdData"
      v-on:totalSubmit="totalSubmit"
    ></basic>
    <!-- end -->
    <!--  -->
    <el-dialog title="" :visible.sync="familyVisible" @close="emptyForm('familyForm')">
      <el-form
        :model="familyForm"
        :inline="true"
        ref="familyForm"
        label-width="140px"
        class="editForm"
      >
        <el-tabs type="card" v-model="familyName" @tab-click="handleTabClick">
          <el-tab-pane label="" name="first">
            <el-row>
              <el-col :span="8" v-if="this.projectState != '4'">
                <el-form-item prop="areaCode">
                  <el-cascader
                    placeholder=""
                    v-model="familyForm.areaCode"
                    :options="areaCodeOptions"
                    :props="areaCodeProps"
                    :disabled="disabledInput"
                    @change="changeAreaCode"
                    clearable
                    change-on-select
                    filterable
                    class="handle-select"
                  />
                </el-form-item>
              </el-col>
              <el-col :span="12" v-if="this.projectState != '4'">
                <el-form-item label="" style="font-size: 20px" prop="personName">
                  <el-input v-model="familyForm.personName" clearable placeholder="">
                    <el-button
                      slot="append"
                      v-if="userBtn['/zcpa/project/screen']"
                      :disabled="disabledState"
                      @click="querybfw"
                    >{{userBtn['/zcpa/project/screen']}}</el-button>
                  </el-input>
                </el-form-item>
              </el-col>
              <el-col :span="2" v-if="this.projectState != '4'">
                <el-button
                  type="primary"
                  v-if="this.projectState != '4'"
                  @click="handledelbfw()"
                  style="margin-top: 4px;"
                ></el-button>
              </el-col>
              <el-table
                v-loading="loading"
                element-loading-text=""
                element-loading-spinner="el-icon-loading"
                :data="bfwForm|filtersTwo"
                border
                class="table"
                ref="multipleTable"
                @selection-change="handleSelectionBox"
              >
                <el-table-column type="selection" width="45" align="center"></el-table-column>
                <el-table-column prop="volunteerVo" label="" width="400" class="zyz">
                  <template slot-scope="scope">
                    <el-button
                      type="text"
                      v-if="index < 4"
                      v-for="(item,index) in scope.row.volunteerVo"
                      :key="index"
                      :disabled="disabledState"
                      @click="handleAddzyz(scope.$index, scope.row,item)"
                      :icon="item.volunteerId?'':'el-icon-circle-plus-outline'"
                      style="font-size: 15px; color: black;min-width: 55px"
                    >{{item.volunteerId?item.name:item.aidTargetName}}</el-button>
                    <i style="border-left: 1px solid black;margin-left: 10px;margin-right: 10px"></i>
                    <el-button
                      type="text"
                      @click="handleVolunteer(scope.$index, scope.row)"
                      :disabled="disabledState"
                      icon="el-icon-delete"
                      style="font-size: 15px;color: black;"
                    ></el-button>
                  </template>
                </el-table-column>
                <el-table-column prop="targetName" label="" width="100"></el-table-column>
                <el-table-column prop="psex" label="" width="100"></el-table-column>
                <el-table-column prop="pidCard" label="" width="200"></el-table-column>
                <el-table-column prop="pcateogry" label="" width="110"></el-table-column>
                <el-table-column prop="phone" label="" width="200"></el-table-column>
                <el-table-column prop="detailAddr" label="" width="200"></el-table-column>
              </el-table>
              <el-pagination
                @size-change="handleService"
                @current-change="handleServiceCurrent"
                :current-page="handleServicePage"
                :page-size="serviceSzie"
                :total="serviceTotal"
                background
                layout="total,sizes,prev, pager, next ,jumper"
              ></el-pagination>
            </el-row>
          </el-tab-pane>
          <el-tab-pane label="" name="second">
            <el-col :span="8" v-if="this.projectState != '4'">
              <el-form-item>
                <el-cascader
                  placeholder=""
                  v-model="familyForm.serviceAreaCode"
                  :options="areaCodeOptions"
                  :props="areaCodeProps"
                  :disabled="disabledInput"
                  @visible-change="cascaderServiceAreaCode"
                  clearable
                  change-on-select
                  filterable
                  class="handle-select mr12"
                />
              </el-form-item>
            </el-col>
            <el-table
              :data="ydjbfwForm|filtersOne"
              border
              class="table"
              ref="multipleTable"
              @selection-change="handleBfwCheck"
            >
              <el-table-column type="selection" width="45" align="center"></el-table-column>
              <el-table-column prop="actFamilyVolunteer" label="" sortable width="320">
                <template slot-scope="scope">
                  <el-button
                    type="text"
                    v-if="index<4"
                    v-for="(item,index) in scope.row.volunteerVo"
                    :key="index"
                    style="font-size: 15px;color: black;width: 55px"
                  >{{item.name}}</el-button>
                  <i style="border-left: 1px solid black;margin-left: 10px;margin-right: 10px"></i>
                  <el-button
                    type="text"
                    @click="handleVolunteer(scope.$index, scope.row)"
                    icon="el-icon-delete"
                    :disabled="disabledState"
                    style="font-size: 15px;color: black;text-align: right"
                  ></el-button>
                </template>
              </el-table-column>
              <el-table-column prop="targetName" label="" width="100"></el-table-column>
              <el-table-column prop="psex" label="" width="100"></el-table-column>
              <el-table-column prop="pidCard" label="" width="220"></el-table-column>
              <el-table-column prop="pcateogry" label="" width="120"></el-table-column>
              <el-table-column prop="phone" label="" sortable width="200"></el-table-column>
              <el-table-column prop="detailAddr" label="" sortable width="200"></el-table-column>
            </el-table>
            <el-pagination
              @size-change="dockingClick"
              @current-change="dockingClickCurrent"
              :current-page="handleDockingPage"
              :page-size="dockingSize"
              :total="dockingTotal"
              background
              layout="total,sizes,prev, pager, next ,jumper"
            ></el-pagination>
          </el-tab-pane>
        </el-tabs>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelArr('familyForm')"></el-button>
      </span>
    </el-dialog>
    <!-- end -->
    <!---->
    <el-dialog title="" :visible.sync="addbfwVisible" left>
      <div class="table-height">
        <el-table
          :data="person"
          style="width: 100%;"
          border
          class="table"
          ref="multipleTable"
          @selection-change="handleAddBfw"
        >
          <el-table-column type="selection" width="55" align="center"></el-table-column>
          <el-table-column prop="personName" label="" style="width: 50%;" border="true"></el-table-column>
          <el-table-column prop="idCard" label="" style="width: 50%;"></el-table-column>
          <el-table-column prop="phone" label="" style="width: 50%;"></el-table-column>
          <el-table-column prop="cateogry" label="" style="width: 50%;"></el-table-column>
        </el-table>
      </div>
      <el-pagination
        @size-change="handleSizeFamily"
        @current-change="handleCurrentFamily"
        :current-page="familyCurrentPage"
        :page-size="familyPagesize"
        :total="familyTotal"
        background
        layout="total,sizes,prev, pager, next ,jumper"
      ></el-pagination>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addbfwVisible = false"></el-button>
        <el-button type="primary" @click="addbfw"></el-button>
      </span>
    </el-dialog>
    <!-- end -->
    <!---->
    <volunteer
      ref="volunteerDialog"
      :volunteercode="volunteercode"
      :category="category"
      :zyzaidTargetType="zyzaidTargetType"
      :dictArrayObj="dictArrayObj"
      v-on:saveAddVolunteer="saveAddVolunteer"
    ></volunteer>
    <!--  -->
    <el-dialog title="" :visible.sync="campusVisible" @close="emptyForm('campusForm')">
      <el-form
        :model="campusForm"
        :inline="true"
        ref="campusForm"
        label-width="140px"
        class="editForm"
      >
        <el-tabs type="card" v-model="campusName" @tab-click="handleActiveClick">
          <el-tab-pane label="" name="first">
            <el-row style="margin-bottom:0;" v-if="this.projectState != '4'">
              <el-col :span="8">
                <el-form-item style="width:100%" prop="areaCode">
                  <el-cascader
                    placeholder=""
                    v-model="campusForm.areaCode"
                    :options="areaCodeOptions"
                    :props="areaCodeProps"
                    @change="changeCampus"
                    clearable
                    change-on-select
                    filterable
                    class="handle-select"
                  />
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="" style="font-size: 20px" prop="schoolName">
                  <el-input v-model="campusForm.schoolName" clearable placeholder="">
                    <el-button
                      slot="append"
                      v-if="userBtn['/zcpa/project/screen']"
                      @click="queryschool"
                    >{{userBtn['/zcpa/project/screen']}}</el-button>
                  </el-input>
                </el-form-item>
              </el-col>
              <el-col :span="2">
                <el-button
                  type="primary"
                  v-if="this.projectState != '4'"
                  @click="handledelCampus()"
                  style="margin-top: 4px;"
                ></el-button>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="2">
                <el-button
                  type="primary"
                  v-if="userBtn['/zcpa/project/push'] && this.projectState != '4'"
                  @click="activityPush()"
                  style="margin-top: 4px;"
                >{{userBtn['/zcpa/project/push']}}</el-button>
              </el-col>
            </el-row>
            <el-table
              :data="schoolForm"
              border
              class="table"
              ref="multipleTable"
              @selection-change="handleSchoolBox"
            >
              <el-table-column type="selection" width="45" align="center"></el-table-column>
              <el-table-column prop="targetName" label="" width="150"></el-table-column>
              <el-table-column prop="actProjectVolunteer" label="" width="400" class="zyz">
                <template slot-scope="scope">
                  <el-button
                    type="text"
                    @click="handleSeeschoolzyz(scope.$index, scope.row)"
                    style="font-size: 15px;"
                  ></el-button>
                  <el-button
                    type="text"
                    v-if="index<3"
                    class="volunteer-number"
                    v-for="(item,index) in scope.row.volunteerVo"
                    :key="index"
                    style="font-size: 15px; color: black;width: 55px"
                  >{{ item.name}}</el-button>
                  <div
                    v-if="scope.row.volunteerVo.length > 3"
                    class="tips"
                    style="display: inline-block;padding: 0 10px 0 0;position: relative; top:-3px;"
                  >...</div>
                  <el-button
                    type="text"
                    :disabled="disabledState"
                    @click="handleAddzyz(scope.$index, scope.row)"
                    icon="el-icon-circle-plus-outline"
                    style="font-size: 15px;color: black;"
                  ></el-button>
                  <i style="border-left: 1px solid black;margin-left: 10px;margin-right: 10px"></i>
                  <el-button
                    type="text"
                    :disabled="disabledState"
                    @click="handleVolunteer(scope.$index, scope.row)"
                    icon="el-icon-delete"
                    style="font-size: 15px;color: black;"
                  ></el-button>
                </template>
              </el-table-column>
              <el-table-column prop="detailAddr" label="" width="200"></el-table-column>
              <el-table-column prop="phone" label=""></el-table-column>
            </el-table>
            <el-pagination
              @size-change="handleService"
              @current-change="handleServiceCurrent"
              :current-page="handleServicePage"
              :page-size="serviceSzie"
              :total="serviceTotal"
              background
              layout="total,sizes,prev, pager, next ,jumper"
            ></el-pagination>
          </el-tab-pane>
          <el-tab-pane label="" name="second">
            <el-row>
              <el-col :span="3">
                <el-button
                  type="primary"
                  v-if="this.projectState != '4'"
                  @click="batchDelete(null)"
                ></el-button>
              </el-col>
            </el-row>
            <div class="record-box">
              <template>
                <el-table
                  :data="recordData"
                  style="width: 100%"
                  @selection-change="handeleActiveCheck"
                >
                  <el-table-column type="selection" width="55"></el-table-column>
                  <el-table-column prop="title" label="" width="180"></el-table-column>
                  <el-table-column prop="startTime" label="" width="180"></el-table-column>
                  <el-table-column prop="endTime" label="" width="180"></el-table-column>
                  <el-table-column prop="areaCode" label="" width="150"></el-table-column>
                  <el-table-column prop="contactTel" label="" width="120"></el-table-column>
                  <el-table-column prop="volunteerNum" label="" width="120"></el-table-column>
                  <el-table-column prop="createDate" label="" width="180"></el-table-column>
                  <el-table-column prop="statusName" label="" width="120"></el-table-column>
                  <el-table-column fixed="right" label="" width="140">
                    <template slot-scope="scope">
                      <el-button type="text" @click="seeCurrent(scope.row)" size="small"></el-button>
                      <el-button
                        type="text"
                        v-if="isDelete"
                        @click="batchDelete(scope.row)"
                        size="small"
                      ></el-button>
                    </template>
                  </el-table-column>
                </el-table>
                <el-pagination
                  @size-change="handleRecord"
                  @current-change="handleRecordCurrent"
                  :current-page="handleRecordPage"
                  :page-size="RecordSzie"
                  :total="RecordTotal"
                  background
                  layout="total,sizes,prev, pager, next ,jumper"
                ></el-pagination>
              </template>
            </div>
          </el-tab-pane>
        </el-tabs>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelArr('campusForm')"></el-button>
      </span>
    </el-dialog>
    <!-- end -->
    <!--  -->
    <active ref="activeData" v-on:submitForm="submitForm"></active>
    <!-- end -->
    <!---->
    <el-dialog title="" :visible.sync="addschoolVisible" left>
      <div class="table-height">
        <el-table
          :data="addschool"
          style="width: 100%;"
          border
          class="table"
          ref="multipleTable"
          @selection-change="handleAddSchool"
        >
          <el-table-column type="selection" width="55" align="center"></el-table-column>
          <el-table-column prop="schoolName" label="" style="width: 50%;" border="true"></el-table-column>
          <el-table-column prop="detailAddr" label="" style="width: 50%;"></el-table-column>
          <el-table-column prop="phone" label="" style="width: 50%;"></el-table-column>
        </el-table>
      </div>
      <el-pagination
        @size-change="handleSizeSchool"
        @current-change="handleCurrentSchool"
        :current-page="schoolCurrentPage"
        :page-size="schoolPagesize"
        :total="schoolTotal"
        background
        layout="total,sizes,prev, pager, next ,jumper"
      ></el-pagination>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addschoolVisible = false"></el-button>
        <el-button type="primary" @click="addfwschool"></el-button>
      </span>
    </el-dialog>
    <!-- end -->
    <!---->
    <el-dialog title="" :visible.sync="SeeschoolVisible" left>
      <div class="ydjizyzy">
        <el-table
          :data="SeezyzData"
          style="width: 100%;"
          border
          class="table"
          ref="multipleTable"
          @selection-change="handleSeeVolunteere"
        >
          <el-table-column type="index" width="55" label="" align="center"></el-table-column>
          <el-table-column prop="name" label="" style="width: 50%;" border="true"></el-table-column>
          <el-table-column prop="idCard" label="" style="width: 50%;"></el-table-column>
          <el-table-column prop="mobile" label="" style="width: 50%;"></el-table-column>
          <el-table-column prop="userName" label="" style="width: 50%;"></el-table-column>
        </el-table>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="SeeschoolVisible = false"></el-button>
      </span>
    </el-dialog>
    <!-- end -->
    <!--  -->
    <el-dialog :visible.sync="otherVisible" :title="otherTitle" @close="emptyForm('otherForm')">
      <el-form
        :model="otherForm"
        :inline="true"
        ref="otherForm"
        label-width="140px"
        class="editForm"
      >
        <el-tabs type="card" v-model="otherName" @tab-click="handleActiveClick">
          <el-tab-pane label="" name="first">
            <el-row v-if="this.projectState != '4'">
              <el-col :span="3">
                <el-button
                  type="primary"
                  v-if="this.projectState != '4'"
                  @click="handleAddzyz()"
                ></el-button>
              </el-col>
              <el-col :span="4">
                <el-button
                  type="primary"
                  v-if="this.projectState != '4'"
                  @click="deleteVolunteer()"
                ></el-button>
              </el-col>
            </el-row>
            <el-table
              :data="otherTabData"
              border
              class="table"
              ref="multipleTable"
              @selection-change="handleOtherBox"
            >
              <el-table-column type="selection" width="45" align="center"></el-table-column>
              <el-table-column prop="name" label="" width="120"></el-table-column>
              <el-table-column prop="sex" label="" width="80"></el-table-column>
              <el-table-column prop="idCard" label="" width="200"></el-table-column>
              <el-table-column prop="mobile" label="" width="180"></el-table-column>
              <el-table-column prop="userType" label="" width="160"></el-table-column>
              <el-table-column prop="createDate" label=""></el-table-column>
            </el-table>
            <el-pagination
              @size-change="handleOtherSize"
              @current-change="handleOtherCurrent"
              :current-page="handleOtherPage"
              :page-size="otherSize"
              :total="otherTotal"
              background
              layout="total,sizes,prev, pager, next ,jumper"
            ></el-pagination>
          </el-tab-pane>
          <el-tab-pane label="" name="second">
            <el-row v-if="this.projectState != '4'">
              <el-col :span="3">
                <el-button
                  type="primary"
                  v-if="userBtn['/zcpa/project/push'] && this.projectState != '4'"
                  @click="activityPush()"
                >{{userBtn['/zcpa/project/push']}}</el-button>
              </el-col>
              <el-col :span="3">
                <el-button
                  type="primary"
                  v-if="this.projectState != '4'"
                  @click="batchDelete(null)"
                ></el-button>
              </el-col>
            </el-row>
            <div class="record-box">
              <template>
                <el-table
                  :data="recordData"
                  style="width: 100%"
                  @selection-change="handeleActiveCheck"
                >
                  <el-table-column type="selection" width="55"></el-table-column>
                  <el-table-column prop="title" label="" width="180"></el-table-column>
                  <el-table-column prop="startTime" label="" width="180"></el-table-column>
                  <el-table-column prop="endTime" label="" width="180"></el-table-column>
                  <el-table-column prop="areaCode" label="" width="150"></el-table-column>
                  <el-table-column prop="contactTel" label="" width="120"></el-table-column>
                  <el-table-column prop="volunteerNum" label="" width="120"></el-table-column>
                  <el-table-column prop="createDate" label="" width="180"></el-table-column>
                  <el-table-column prop="statusName" label="" width="120"></el-table-column>
                  <el-table-column fixed="right" label="" width="140">
                    <template slot-scope="scope">
                      <el-button type="text" @click="seeCurrent(scope.row)" size="small"></el-button>
                      <el-button
                        type="text"
                        v-if="isDelete"
                        @click="batchDelete(scope.row)"
                        size="small"
                      ></el-button>
                    </template>
                  </el-table-column>
                </el-table>
                <el-pagination
                  @size-change="handleRecord"
                  @current-change="handleRecordCurrent"
                  :current-page="handleRecordPage"
                  :page-size="RecordSzie"
                  :total="RecordTotal"
                  background
                  layout="total,sizes,prev, pager, next ,jumper"
                ></el-pagination>
              </template>
            </div>
          </el-tab-pane>
        </el-tabs>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelArr('otherForm')"></el-button>
      </span>
    </el-dialog>
    <!-- end -->
    <!---->
    <see ref="seeData" :seeId="seeId" :seeRow="seeRow" :dictArrayObj="dictArrayObj"></see>
    <!-- end -->
    <!---->
    <deleteVolunteers ref="volunteerData" :title="titles" v-on:deleteVolunteers="deleteVolunteers"></deleteVolunteers>
    <!-- end -->
    <!--  -->
    <seeActive
      ref="seeDatas"
      :seeByDada="seeByDada"
      :dictArrayObj="dictArrayObj"
      :category="category"
      :projectId="projectId"
      :areaCode="areaCode"
      :targetId="targetId"
    ></seeActive>
    <!-- end -->
  </div>
</template>

<script>
import {
  getList,
  create,
  getById,
  updateState,
  deleteById,
  update,
  page,
  bfwcreate,
  schoolcreate,
  zyzupdate,
  batchUpdate,
  bfwdeleteByIds,
  actSchoolpage,
  actSchoolcreate,
  schooldeleteByIds,
  selectByProjectId,
  schoolbfwdeleteByIds,
  familyCampusById,
  otherById,
  serviceObject,
  activeCreate,
  activePage,
  getActive,
  deleteActive,
  seePersonnel
} from "@/api/zcpa/actProject";
import { getAreaCode } from "@/api/zcpa/baseVolunteer";
import basic from "../publics/basic.vue";
import deleteVolunteers from "../publics/deleteVolunteers.vue";
import see from "../publics/see.vue";
import volunteer from "../publics/volunteer.vue";
import active from "../publics/active.vue";
import seeActive from "../publics/seeActive.vue";
import { getAreaTree } from "@/api/sys";
import { mapGetters } from "vuex";

export default {
  name: "basetable",
  components: {
    basic,
    deleteVolunteers,
    see,
    volunteer,
    active,
    seeActive
  },
  data() {
    return {
      areaCode: "", //areaCode
      submitType: "", //
      byIdData: {}, //
      seeByDada: {}, //
      xzZoning: [], //
      campusAreaCode: [], //
      currentPage: 1, //
      pagesize: 10, //  
      total: 0, //
      schoolTotal: 0, //
      schoolPagesize: 10, //
      schoolCurrentPage: 1, //
      familyTotal: 0, //
      familyPagesize: 10, //
      familyCurrentPage: 1, //
      previeVisible: false, //
      schoolTargetID: "", //ID
      projectState: "", //
      disabledState: false, //
      isTips: false, //...
      volunteerArr: "", //
      rows: [], //table
      needNumll: "", //
      bfwForm: [], //table
      ydjbfwForm: [], //table
      schoolForm: [], //table
      cityCountyOptions: [], //2
      disabledInput: false, //
      zyzaidTargetType: "",
      isDelete: true, //
      category: "", //
      projectId: "", //ID
      targetId: "", //ID
      realNumber: "", //
      recordData: [], //tabData
      isShow: false, //
      titleName: "", //,
      titles: "", //
      familyVisible: false, //
      familyName: "first", //tabs
      handleServicePage: 1, //,page
      serviceSzie: 10, //page
      serviceTotal: 0, //page
      handleDockingPage: 1, //page
      dockingSize: 10, //page
      dockingTotal: 0, //page
      handleOtherPage: 1, //page
      otherSize: 10, //page
      otherTotal: 0, //page
      handleRecordPage: 1, //Page
      RecordSzie: 10, //Page
      RecordTotal: 0, //Page
      volunteercode: "", //areacode
      familyForm: {
        personName: "", //personName
        areaCode: "", //areacode
        areaCodeType: "", //areacodename
        maxLengthValue: "", //areacode
        serviceAreaCode: "", //areacode
        serviceAreaCodeType: "", //areacodename
        maxLengthCode: "" //areacode
      },
      campusVisible: false, //
      campusName: "first", //tabs
      campusForm: {
        areaCode: "", //areacodecode
        areaCodeType: "", //areacodename
        schoolName: "",
        maxLengthValue: "" //areacode
      },
      //
      otherVisible: false, //
      otherTitle: "", //
      otherName: "first", //table
      otherTabData: [], //
      otherForm: {}, //
      loading: false, //loading
      person: [], //table
      addschool: [], //table
      volunteer: [], //table
      SeezyzData: [], //table
      projectTypeArr: [], //
      examineVisible: false, //
      addbfwVisible: false, //
      addschoolVisible: false, //
      SeeschoolVisible: false, //
      areaCodeOptions: [], //
      areaCodeProps: {
        value: "id",
        label: "text"
      },
      delbfwzyz: [], //table
      delschoolzyz: [], //table
      school: {
        areaCodeValue: "", //areacodecode
        areaCodeType: "", //areacodename
        schoolName: "", //
        maxLengthValue: "" //areacode
      },
      bfw: {
        areaCodeValue: "", //areacodecode
        areaCodeType: "", //areacodename
        personName: "",
        maxLengthValue: "" //areacode
      },
      query: {
        //
        startTime: "",
        endTime: "",
        region: "",
        areaCode: "",
        category: "",
        projectLevel: "",
        projectState: "",
        projectStateType: ""
      },
      // 
      startTime: {
        disabledDate: time => {
          if (this.query.endTime) {
            return time.getTime() > new Date(this.query.endTime).getTime();
          }
        }
      },
      // 
      endTime: {
        disabledDate: time => {
          if (this.query.startTime) {
            return (
              time.getTime() < new Date(this.query.startTime).getTime() - 8.64e7
            );
          }
        }
      },
      updateState: {
        //
        projectState: 2
      },
      hackReset: true, //
      bfwcreate: {
        actFamilyTarget: []
      },
      schoolcreate: {
        actProjectTarget: [
          {
            areaCode: "",
            targetId: ""
          }
        ],
        projectId: ""
      },
      zyz: {
        name: "",
        userType: ""
      },
      zyzupdate: {
        id: "",
        areaCode: "",
        targetId: "",
        volunteerId: "",
        projectId: "",
        aidTargetType: ""
      },
      zyzcreate: {
        actProjectVolunteer: [
          {
            areaCode: "",
            volunteerId: ""
          }
        ],
        targetId: "",
        projectId: ""
      },
      otherzyzcreate: {
        actProjectVolunteer: [
          {
            areaCode: "",
            volunteerId: ""
          }
        ],
        projectId: ""
      },
      idx: "",
      id: "", //ID
      seeId: "", //ID
      seeRow: {} //row
    };
  },
  created() {
    this.getinformation();
    // this.AreaCodeQuery();
    this.RegionCode();
    this.initDictData();
    this.InitializationCode();
  },

  methods: {
    //
    getinformation() {
      this.loading = false;
      getAreaCode().then(response => {
        if (response.status == 1) {
          this.loading = true;
          this.areaCode = response.data.areaCode;
          this.areaName = response.data.areaName;
          this.volunteercode = response.data.areaCode;
          this.getData();
          this.AreaCodeQuery(this.areaCode);
        }
      });
    },
    handleQuery: function() {
      this.currentPage = 1;
      this.getData();
    },
    handleSizeChange: function(size) {
      this.pagesize = size;
      this.getData();
      //
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
      //
      this.getData();
    },
    // 
    getData() {
      this.query = Object.assign(this.query, {
        areaCode: this.areaCode,
        region: this.areaCodeObj,
        pageNum: this.currentPage,
        pageSize: this.pagesize,
        totalRecord: this.total
      });
      this.loading = true;
      getList(this.query).then(response => {
        if (response.status == 1) {
          this.rows = this.formatDataList(response.rows);
          this.total = response.total;
          this.rows = this.UserAreaCode(response.rows);
        }
      });
    },
    formatDataList(data) {
      var projectType = this.projectTypeArr;
      for (var i = 0; i < data.length; i++) {
        for (var y = 0; y < projectType.length; y++) {
          if (data[i].category == projectType[y].code) {
            data[i].categoryType = projectType[y].name;
          }
        }
      }
      return data;
    },
    //
    UserAreaCode(data) {
      for (var i = 0; i < data.length; i++) {
        data[i].userAreaCode = this.areaCode;
      }
      return data;
    },
    //
    search() {
      if (this.areaCode == "4307") {
        let areaCodes = this.xzZoning;
        if (areaCodes.length > 0) {
          for (let e of this.xzZoning) {
            this.areaCodeObj = e;
          }
        } else {
          this.areaCodeObj = "";
        }
      }
      this.handleQuery();
    },
    //
    changeState(value) {
      var obj = {};
      obj = this.statusValueArr.find(item => {
        //
        return item.code == value;
      });
      if (value) {
        if (value == 0) {
          this.query.projectState = ""; //code
          this.query.projectStateType = ""; //code
        } else {
          this.query.projectState = value; //code
        }
      }
    },
    //
    totalSubmit() {
      this.addsForm = this.$refs.basicData.addsForm; //
      if (this.submitType == "add") {
        this.addsForm.projectLevel = 0;
        create(this.addsForm).then(response => {
          if (response.status == 1) {
            this.$message.success("");
            this.$emit("refreshSubcomponents");
          }
        });
      }
      if (this.submitType == "edit") {
        if (this.addsForm.peopleNumber < this.realNumber) {
          this.$message({
            message:
              "" +
              this.realNumber +
              ",",
            type: "error"
          });
          return false;
        } else {
          update(this.addsForm).then(response => {
            if (response.status == 1) {
              this.$message.success("");
              this.$emit("refreshSubcomponents");
            }
          });
        }
      }
      if (this.submitType == "declare") {
        create(this.addsForm).then(response => {
          if (response.status == 1) {
            this.$message.success("");
            this.$emit("refreshSubcomponents");
          }
        });
      }
    },
    //
    handleSizeSchool: function(size) {
      this.schoolPagesize = size;
      this.getSchoolPage();
    },
    handleCurrentSchool: function(currentPage) {
      this.schoolCurrentPage = currentPage;
      this.getSchoolPage();
    },
    getSchoolPage() {
      var obj = {};
      obj.pageNum = this.schoolCurrentPage;
      obj.pageSize = this.schoolPagesize;
      obj.totalRecord = this.schoolTotal;
      obj.areaCode = this.areaCode;
      actSchoolpage(obj).then(response => {
        if (response.status == 1) {
          this.schoolTotal = response.total;
          this.addschool = response.rows;
        }
      });
    },
    //
    handleSizeFamily: function(size) {
      this.familyPagesize = size;
      this.getFamilyPage();
    },
    handleCurrentFamily: function(currentPage) {
      this.familyCurrentPage = currentPage;
      this.getFamilyPage();
    },
    getFamilyPage() {
      var obj = {};
      obj.pageNum = this.familyCurrentPage;
      obj.pageSize = this.familyPagesize;
      obj.totalRecord = this.familyTotal;
      obj.areaCode = this.familyForm.maxLengthValue;
      page(obj).then(response => {
        if (response.status == 1) {
          this.schoolTotal = response.total;
          this.person = response.rows;
        }
      });
    },
    //
    handleDocking(row) {
      //1 2 3  4 5 6
      this.loading = true;
      this.projectId = row.id;
      this.category = row.category;
      this.projectState = row.projectState;
      var areaCodesObj = [];
      var arr = [4, 6, 9, 12];
      if (this.projectState == "4") {
        this.isDelete = false;
      }
      for (var i = 0; i < 4; i++) {
        areaCodesObj[i] = this.areaCode.substring(0, arr[i]);
      }
      for (let e of areaCodesObj) {
        this.areaCodeArr = e;
      }
      if (row.category == 1) {
        this.familyVisible = true;
        this.familyName = "first";
        if (row.projectState == "4") {
          this.disabledState = true;
        } else {
          this.disabledState = false;
        }
        this.familyForm.areaCode = this.areaCode;
        this.familyForm.serviceAreaCode = this.areaCode;
        this.familyForm.maxLengthValue = this.areaCodeArr;
        this.getServicePage();
      }
      if (row.category == 2) {
        this.campusVisible = true;
        this.campusName = "first";
        if (row.projectState == "4") {
          this.disabledState = true;
        } else {
          this.disabledState = false;
        }
        this.campusForm.areaCode = this.areaCode;
        this.campusForm.maxLengthValue = this.areaCodeArr;
        this.getServicePage();
      }
      if (
        row.category == 3 ||
        row.category == 4 ||
        row.category == 5 ||
        row.category == 6
      ) {
        this.otherVisible = true;
        this.otherTitle = "";
        this.otherName = "first";
        this.getOtherPage();
      }
      if (row.category == 4) {
        this.otherTitle = "";
      }
      if (row.category == 5) {
        this.otherTitle = "";
      }
      if (row.category == 6) {
        this.otherTitle = "";
      }
    },
    //page
    getOtherPage() {
      var obj = {};
      obj.pageNum = this.handleOtherPage;
      obj.pageSize = this.otherSize;
      obj.totalRecord = this.otherTotal;
      obj.projectId = this.projectId;
      obj.areaCode = this.areaCode;
      otherById(obj).then(response => {
        if (response.status == 1) {
          this.loading = false;
          this.otherTabData = response.rows;
          this.otherTotal = response.total;
          this.realNumber = response.total;
          this.InitializationCode();
        }
      });
    },
    //page
    getServicePage() {
      var obj = {};
      obj.pageNum = this.handleServicePage;
      obj.pageSize = this.serviceSzie;
      obj.totalRecord = this.serviceTotal;
      obj.projectId = this.projectId;
      obj.category = this.category;
      obj.areaCode = this.areaCode;
      familyCampusById(obj).then(response => {
        if (response.status == 1) {
          this.loading = false;
          if (this.category == 1) {
            this.bfwForm = this.initVolunteer(response.rows);
            this.serviceTotal = response.total;
            this.InitializationCode();
          }
          if (this.category == 2) {
            this.schoolForm = response.rows;
            this.serviceTotal = response.total;
          }
        }
      });
    },
    //
    initVolunteer(data) {
      for (var j = 0; j < data.length; j++) {
        for (var i = 0; i < data[j].volunteerVo.length; i++) {
          if (data[j].volunteerVo[i].name == null) {
            data[j].volunteerVo[i].volunteerId = "";
          }
        }
      }
      return data;
    },
    //page
    handleActiveClick(tab, event) {
      if (tab.name == "second") {
        if (this.category == "2") {
          this.activeList();
        } else {
          this.activeList();
        }
      }
    },
    //page
    handleService: function(size) {
      this.serviceSzie = size;
      this.getServicePage();
    },
    //page
    handleServiceCurrent: function(currentPage) {
      this.handleServicePage = currentPage;
      this.getServicePage();
    },
    //page
    handleOtherSize: function(size) {
      this.otherSize = size;
      this.getOtherPage();
    },
    handleOtherCurrent: function(currentPage) {
      this.handleOtherPage = currentPage;
      this.getOtherPage();
    },
    //page
    handleRecord: function(size) {
      this.RecordSzie = size;
      this.activeList();
    },
    handleRecordCurrent: function(currentPage) {
      this.handleRecordPage = currentPage;
      this.activeList();
    },
    //
    dockingService() {
      var obj = {};
      obj.projectId = this.projectId;
      obj.areaCode = this.areaCode;
      obj.pageNum = this.handleDockingPage;
      obj.pageSize = this.dockingSize;
      obj.totalRecord = this.dockingTotal;
      serviceObject(obj).then(response => {
        if (response.status == 1) {
          this.ydjbfwForm = response.rows;
          this.dockingTotal = response.total;
          this.InitializationCode();
        }
      });
    },
    //page
    dockingClick: function(size) {
      this.dockingSize = size;
      this.dockingService();
    },
    dockingClickCurrent: function(currentPage) {
      this.handleDockingPage = currentPage;
      this.dockingService();
    },
    //tab
    handleTabClick(tab, event) {
      this.tabName = tab.name;
      if (tab.name == "second") {
        this.dockingService();
      }
    },
    //
    deleteVolunteer() {
      if (this.otherBox && this.otherBox.length) {
        var tempArr = this.otherBox;
        var strArr = this.otherBox.map(item => item.id).join(",");
        if (tempArr.length > 0) {
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          }).then(() => {
            schooldeleteByIds(strArr).then(response => {
              if (response.status == 1) {
                this.handleOtherPage = 1;
                this.$message.success("");
                this.$refs.volunteerDialog.getVolunteerData();
                this.getOtherPage();
                this.getNeedNum();
              }
            });
          });
        }
      } else {
        this.$message({
          message: "",
          type: "warning"
        });
      }
    },
    //
    querybfw() {
      var query = {};
      query.areaCode = this.familyForm.maxLengthValue;
      query.personName = this.familyForm.personName;
      page(query).then(response => {
        if (response.status == 1) {
          this.person = response.rows;
          this.addbfwVisible = true;
          this.familyTotal = response.total;
        }
      });
    },
    //
    addbfw() {
      if (this.addBfwCheck && this.addBfwCheck.length) {
        var FamilyTarget = [];
        for (var i = 0; i < this.addBfwCheck.length; i++) {
          var obj = {};
          obj.areaCode = this.addBfwCheck[i].areaCode;
          obj.personId = this.addBfwCheck[i].id;
          obj.personName = this.addBfwCheck[i].personName;
          FamilyTarget.push(obj);
        }
        this.bfwcreate.actFamilyTarget = FamilyTarget;
        this.bfwcreate.projectId = this.projectId;
        this.bfwcreate.areaCode = this.familyForm.maxLengthValue;
        if (this.addBfwCheck.length > 0) {
          bfwcreate(this.bfwcreate).then(response => {
            if (response.status == 1) {
              this.$message.success(``);
              this.addbfwVisible = false;
              this.getServicePage();
            }
          });
        }
      } else {
        this.$message.success(``);
        this.addbfwVisible = false;
      }
    },
    //
    handleAddzyz(index, row, item) {
      this.$refs.volunteerDialog.volunteerVisible = true;
      this.idx = index;
      if (this.category == 1) {
        this.zyzupdate.targetId = row.id;
        this.zyzAreaCode = row.areaCode;
