    initDictData() {
      this.genderArr = this.dictArrayObj.genderValue; //4
      this.projectTypeArr = this.dictArrayObj.projectValue; //47
      this.projectLevelArr = this.dictArrayObj.ProjectLevelValue; //48
      this.peopleValueArr = this.dictArrayObj.peopleValue; //49
      this.statusValueArr = this.dictArrayObj.statusValue; //50
      this.serviceObjectArr = this.dictArrayObj.serviceObjectValue; //51
    },
    //areaCode
    initUnitForm(areaCodeArr, areaCodeOptions) {
      if (areaCodeArr && areaCodeOptions) {
        //
        var maxLengthAreaCode = this.globalMethods.findLongestWord(
          [].concat(areaCodeArr)
        );
        //
        var tepmTreeArr = this.globalMethods.getCascaderObj(
          areaCodeArr,
          areaCodeOptions
        );
        //
        // var tempTextArr = [];
        // if (tepmTreeArr.length > 0) {
        //   tepmTreeArr.forEach((item, i) => {
        //     tempTextArr.push(item.text);
        //   });
        // }
        //
        // this.familyForm.areaCodeType = tempTextArr;
        this.familyForm.areaCode = areaCodeArr;
        this.familyForm.maxLengthValue = maxLengthAreaCode; //
        //areaCode
        // this.familyForm.serviceAreaCode = tempTextArr;
        this.familyForm.serviceAreaCode = areaCodeArr;
        this.familyForm.maxLengthCode = maxLengthAreaCode; //

        //
        // this.campusForm.areaCodeType = tempTextArr;
        this.campusForm.areaCode = areaCodeArr;
        this.campusForm.maxLengthValue = maxLengthAreaCode; //
      }
    },
    //
    changeAreaCode(value) {
      // var tempTextArr = [];
      // // code
      // var tepmTreeArr = this.globalMethods.getCascaderObj(
      //   value,
      //   this.areaCodeOptions
      // );
      var maxLengthValue = this.globalMethods.findLongestWord([].concat(value));
      this.familyForm.areaCodeValue = value; //code
      this.familyForm.maxLengthValue = maxLengthValue; //
    },
    //
    cascaderServiceAreaCode(value) {
      if (!value) {
        var maxLengthValue = this.globalMethods.findLongestWord(
          [].concat(this.familyForm.serviceAreaCode)
        );
        this.areaCode = maxLengthValue;
        this.dockingService();
      }
    },
    //
    changeCampus(value) {
      // var tempTextArr = [];
      // // code
      // var tepmTreeArr = this.globalMethods.getCascaderObj(
      //   value,
      //   this.areaCodeOptions
      // );
      // if (tepmTreeArr.length > 0) {
      //   tepmTreeArr.forEach((item, i) => {
      //     tempTextArr.push(item.text);
      //   });
      // }
      var maxLengthValue = this.globalMethods.findLongestWord([].concat(value));
      this.campusForm.areaCodeValue = value; //code
      // this.campusForm.areaCodeType = tempTextArr; //
      this.campusForm.maxLengthValue = maxLengthValue; //
    }
  },
  filters: {
    filtersOne(data) {
      var item = [];
      if (data) {
        for (var i = 0; i < data.length; i++) {
          var c = data[i].volunteerVo;
          if (
            c[0].volunteerId &&
            c[1].volunteerId &&
            c[2].volunteerId &&
            c[3].volunteerId
          ) {
            item.push(data[i]);
          }
        }
      }
      return item;
    },
    filtersTwo(data) {
      var item = [];
      if (data) {
        for (var i = 0; i < data.length; i++) {
          var c = data[i].volunteerVo;
          if (
            c[0].volunteerId &&
            c[1].volunteerId &&
            c[2].volunteerId &&
            c[3].volunteerId
          ) {
          } else {
            item.push(data[i]);
          }
        }
      }
      return item;
    }
  },
  computed: {
    ...mapGetters(["dictArrayObj"]),
    ...mapGetters(["userProp"]),
    ...mapGetters(["userBtn"])
  }
};
</script>

