<template>
  <div class="active-box">
    <el-dialog title="" :visible.sync="activityVisible" @close="emptyForm('activitysForm')">
      <el-form
        :model="activitysForm"
        :inline="true"
        :rules="activitysRules"
        ref="activitysForm"
        label-width="120px"
        class="activitysBox"
      >
        <el-row>
          <el-col :span="24">
            <el-form-item label="" prop="title">
              <el-input
                maxlength="20"
                v-model="activitysForm.title"
                clearable
                show-word-limit
                placeholder=",20"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="startTime">
              <el-date-picker
                v-model="activitysForm.startTime"
                type="datetime"
                @change="startChange()"
                :picker-options="startPicker"
                clearable
                default-time="08:00:00"
                placeholder=""
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="endTime">
              <el-date-picker
                v-model="activitysForm.endTime"
                :picker-options="endPicker"
                clearable
                :disabled="isDisabled"
                type="datetime"
                placeholder=""
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="areaCode">
              <el-input
                class="address-right"
                maxlength="20"
                v-model="activitysForm.areaCode"
                clearable
                show-word-limit
                placeholder="20"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="contactName">
              <el-input v-model="activitysForm.contactName" clearable placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="contactTel">
              <el-input v-model="activitysForm.contactTel" clearable placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="" prop="content">
              <el-input
                type="textarea"
                :rows="6"
                maxlength="50"
                v-model="activitysForm.content"
                clearable
                show-word-limit
                placeholder="50"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelArr('activitysForm')"> </el-button>
        <el-button type="primary" @click="submitForm('activitysForm')"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  naem: "active",
  data() {
    return {
      isDisabled: true,
      activityVisible: false,
      startPicker: this.startPickers(), //
      endPicker: this.endPickers(), //
      //
      activitysForm: {
        title: "", //
        startTime: "", //
        endTime: "", //
        areaCode: "", //
        contactName: "", //
        contactTel: "", //
        currentAreaCode: "", //areaCode
        content: "" //
      },
      activitysRules: {
        title: [
          {
            required: true,
            message: ",20",
            trigger: "blur"
          }
        ],
        startTime: [
          { required: true, message: "", trigger: "change" }
        ],
        endTime: [
          { required: true, message: "", trigger: "change" }
        ],
        areaCode: [
          {
            required: true,
            message: ",20",
            trigger: "blur"
          }
        ],
        contactName: [
          { required: true, message: "", trigger: "blur" }
        ],
        contactTel: [
          { required: true, message: "", trigger: "blur" },
          { validator: this.globalMethods.checkPhone, trigger: "blur" }
        ],
        content: [
          {
            required: true,
            message: "50",
            trigger: "blur"
          },
          { validator: this.globalMethods.checkTextareaone, trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    //
    emptyForm(type) {
      if (type) {
        this.$refs[type].resetFields();
      }
    },
    //
    startChange() {
      if (this.activitysForm.startTime) {
        this.isDisabled = false;
      }
    },
    //
    cancelArr(formName) {
      this.$nextTick(() => {
        this.$refs[formName].resetFields();
      });
      this.activityVisible = false;
    },
    //
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          //
          this.$emit("submitForm");
        } else {
          return false;
        }
      });
    },
    //
    startPickers() {
      // const self = this;
      // return {
      //   disabledDate(time) {
      //     if (self.activitysForm.endTime) {
      //       //
      //       return (
      //         new Date(self.activitysForm.endTime).getTime() < time.getTime()
      //       );
      //     } else {
      //       // return time.getTime() > Date.now()//
      //     }
      //   }
      // };
    },
    //
    endPickers() {
      const self = this;
      return {
        disabledDate(time) {
          if (self.activitysForm.startTime) {
            //
            return (
              new Date(self.activitysForm.startTime).getTime() - 8.64e7 >
              time.getTime()
            );
          } else {
            return (
              new Date(self.activitysForm.startTime).getTime() > time.getTime()
            );
          }
        }
      };
    }
  }
};
</script>

<style lang="less" scoped>
.activitysBox {
  /deep/ .el-form-item {
    width: 100%;
  }
  /deep/ .el-form-item__content {
    width: calc(~"100% - 160px");
  }
  /deep/ .el-date-editor.el-input {
    width: 100%;
  }
  /deep/ .address-right .el-input__inner {
    padding-right: 90px;
  }
  /deep/ .el-textarea__inner {
    word-break: break-all;
  }
}
</style>