<template>
  <div class="volunteer">
    <el-dialog :title="titleName" :visible.sync="delzyzVisible">
      <template>
        <div class="zyz">
          <el-table
            ref="multipleTable"
            :data="deleteVolunteerArr"
            style="width: 100%"
            @selection-change="handleDeleteCheck"
          >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="name" label="" width="120"></el-table-column>
            <el-table-column prop="idCard" label="" width="220"></el-table-column>
            <el-table-column prop="mobile" label="" width="200"></el-table-column>
            <el-table-column prop="userType" label=""></el-table-column>
          </el-table>
        </div>
      </template>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delzyzVisible = false"> </el-button>
        <el-button type="primary" @click="deleteVolunteers"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String
    }
  },
  watch: {
    //
    title: {
      handler(newName, oldName) {
        if (newName) {
          this.titleName = newName;
        }
      },
      immediate: true, //
      deep: true
    }
  },
  name: "deleteVolunteer",
  data() {
    return {
      titleName: "", //
      delzyzVisible: false, //
      deleteVolunteerArr: [] //table
    };
  },
  methods: {
    //
    deleteVolunteers() {
      this.$emit("deleteVolunteers");
    },
    //
    handleDeleteCheck(val) {
      this.deleteCheck = val;
      
    }
  }
};
</script>

